<template>
  <div :id="chartId" style="background-color: white;text-align: center;">
    <span v-if="chartData.length == 0" class="chart-message-area" style="">{{chartOptions.chart.emptyDataText}}</span>
  </div>
</template>
<script>
import BaseChart from './BaseChart'

/**
 * 说明: 0 维统计图表
 */
export default {
  name: 'CPie',
  extends: BaseChart,
  props: {
    height: {
      type: Number,
      default: 300
    }
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      this.load()
    },
    load () {
      this.draw()
    },
    draw () {
      let total = 0
      this.chartData.forEach(item => {
        total += item.value
      })
      if (typeof this.chartOptions.chart.padding === 'undefined') {
        this.chartOptions.chart.padding = [50, 0]
      }
      let value = this.chartOptions.value
      let chart = this.getChart()
      chart.source(this.chartData, {
        value: {
          formatter: function formatter (val) {
            val = parseFloat((val / (total || 1)) * 100).toFixed(2) + '%'
            return val
          }
        }
      })
      chart.coord('theta', {
        radius: this.chartOptions.chart.radius || 0.65
      })
      chart.tooltip({
        showTitle: false,
        itemTpl: '<li><span style="background-color:{color};" class="g2-tooltip-marker"></span>{name}: {value}</li>'
      })
      let interval = chart.intervalStack().position('value').color('label').label('value', {
        formatter: (value.formatter || function (val, item) {
          return item.point.label + ': ' + item.point.value + (value.suffix)
        })
      }).style({
        lineWidth: 1,
        stroke: '#fff'
      })
      chart.legend({
        clickable: false
      })
      chart.render()
      if (this.chartData && this.chartData.length > 0) {
        interval.setSelected(this.chartData[0])
      }
    }/* ,
    filterData (data) {
      if (data && data.length > 0) {
        let label = this.chartOptions.properties.label
        let value = this.chartOptions.properties.value
        data.forEach(item => {
          if (label) {
            item.label = item[label]
          }
          if (value) {
            item.value = item[value]
          }
        })
      }
      this.chartData = data || []
    } */
  }
}
</script>

<style scoped>

</style>



