let util = {
  /***
   * 生成不重复的ID
   */
  id: (function () {
    var id = 1000
    var getId = function () {
      id += 1
      return 'g2_chart_' + id
    }
    return getId
  })()
}
util.Color = {
  /**
   * 获取颜色数组
   * @param type 指定图表类型
   * @returns {*}
   */
  getColors (type) {
    let map = {
      default: ['#1890FF', '#13C2C2', '#2FC25B', '#FACC14', '#F04864', '#8543E0', '#3436C7', '#223273'],
      area: ['#1890FFB2', '#2FC25BB2', '#13C2C2B2', '#FACC14B2', '#F04864B2', '#8543E0B2', '#3436C7B2', '#223273B2']
    }
    return map[type || 'default'] || map['default']
  }
}

export default util



