<template>
  <div :id="chartId" style="background-color: white;text-align: center;">
    <span v-if="chartData.length == 0" class="chart-message-area" style="">{{chartOptions.chart.emptyDataText}}</span>
  </div>
</template>

<script>
import BaseChart from './BaseChart'
import DataSet from '@antv/data-set'

export default {
  name: 'CLine',
  extends: BaseChart,
  data () {
    return {
      chartData: [{
        'month': '一月',
        '东京': 7,
        '伦敦': 3.0,
        '上海': 3.9
      }, {
        'month': '二月',
        '东京': 7,
        '伦敦': 4.0,
        '上海': 5.9
      }, {
        'month': '三月',
        '东京': 10,
        '伦敦': 6.0,
        '上海': 7.9
      }, {
        'month': '四月',
        '东京': 9,
        '伦敦': 3.6,
        '上海': 3.9
      }, {
        'month': '五月',
        '东京': 4,
        '伦敦': 3.1,
        '上海': 6.9
      }, {
        'month': '六月',
        '东京': 6.7,
        '伦敦': 3.9,
        '上海': 4.1
      }, {
        'month': '七月',
        '东京': 8.1,
        '伦敦': 4.3,
        '上海': 4.9
      }, {
        'month': '八月',
        '东京': 7.2,
        '伦敦': 5.0,
        '上海': 3.89
      }, {
        'month': '九月',
        '东京': 6.5,
        '伦敦': 4.35,
        '上海': 5.89
      }, {
        'month': '十月',
        '东京': 7,
        '伦敦': 3.56,
        '上海': 3.9
      }, {
        'month': '十一月',
        '东京': 7.23,
        '伦敦': 4.2,
        '上海': 5.29
      }, {
        'month': '十二月',
        '东京': 6.56,
        '伦敦': 3.65,
        '上海': 4.89
      }]
    }
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      this.load()
    },
    load () {
      this.draw()
    },
    draw () {
      if (typeof this.chartOptions.chart.padding === 'undefined') {
        this.chartOptions.chart.padding = [30, 'auto', 70, 'auto']
      }
      // 指标，维度解析
      // let dimension = this.chartOptions.chart.dimension
      // let metrics = this.chartOptions.chart.metrics
      let chart = this.getChart()
      // todo 这里 维度 等同于 value 属性 ?
      // chart.source(this.chartData, {})
      var dv = new DataSet.View().source(this.chartData)
      dv.transform({
        type: 'fold',
        fields: ['东京', '伦敦', '上海'], // fields 指标, year 维度
        key: 'type',
        value: 'value'
      })

      chart.source(dv, {
        value: this.copy({
          formatter: function formatter (val) {
            return val + '°C'
          }
        }, {})
      })

      chart.tooltip({
        crosshairs: {
          type: 'line'
        }
      })
      // chart.axis('temperature', {
      //   label: {
      //     formatter: function formatter (val) {
      //       return val + '°C'
      //     }
      //   }
      // })
      chart.line().position('month*value').color('type')
      chart.point().position('month*value').color('type').size(4).shape('circle').style({
        stroke: '#fff',
        lineWidth: 1
      })
      chart.render()
    }
  }
}
</script>

<style scoped>

</style>



