<template>
  <div :id="chartId" style="background-color: white;text-align: center;">
    <span v-if="chartData.length == 0" class="chart-message-area" style="">{{chartOptions.chart.emptyDataText}}</span>
  </div>
</template>

<script>
import BaseChart from './BaseChart'

export default {
  name: 'CBar',
  extends: BaseChart,
  props: {
    height: {
      default: 400,
      type: Number
    }
  },
  data () {
    return {}
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      this.load()
    },
    load () {
      this.draw()
    },
    draw () {
      if (typeof this.chartOptions.chart.padding === 'undefined') {
        this.chartOptions.chart.padding = [20, 20, 50, 'auto']
      }
      let value = this.chartOptions.value
      let exValue = this.copy({}, value, ['formatter', 'prefix', 'suffix'])
      let chart = this.getChart()
      chart.source(this.chartData, {
        value: this.copy({
          nice: true,
          alias: ''
        }, exValue)
      })
      chart.axis('label', {
        label: {
          textStyle: {
            fill: '#8d8d8d',
            fontSize: 12
          }
        },
        tickLine: {
          alignWithLabel: false,
          length: 0
        },
        line: {
          lineWidth: 0
        }
      })
      chart.axis('value', {
        label: null,
        title: {
          offset: 30,
          textStyle: {
            fontSize: 12,
            fontWeight: 300
          }
        }
      })
      chart.legend(false)
      chart.coord().transpose()
      chart.interval().position('label*value').opacity(1).label('value', {
        textStyle: {
          fill: '#8d8d8d'
        },
        offset: 10
      })
      chart.render()
    }
  }
}
</script>

<style scoped>

</style>



