<template>
  <div :id="chartId" style="background-color: white;text-align: center;">
    <span v-if="chartData.length == 0" class="chart-message-area" style="">{{chartOptions.chart.emptyDataText}}</span>
  </div>
</template>
<script>
import BaseChart from './BaseChart'
/**
 * 水波图(默认接收 0维1指标， 支持多指标)
 */
export default {
  name: 'LiquidFillGauge',
  extends: BaseChart,
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      this.load()
    },
    load () {
      this.draw()
    },
    draw () {
      if (typeof this.chartOptions.chart.padding === 'undefined') {
        this.chartOptions.chart.padding = ['auto']
      }
      let chart = this.getChart()
      chart.source(this.chartData, {
        value: {
          min: 0,
          max: 100
        }
      })
      chart.legend(false)
      chart.axis(false)
      chart.interval().position('label*value').color('label').shape('liquid-fill-gauge').style({
        lineWidth: 8,
        opacity: 0.45
      })

      this.chartData.forEach(function (row) {
        chart.guide().text({
          top: true,
          position: {
            label: row.label,
            value: 50 // 30-80 范围 效果好
          },
          content: row.value + '%',
          style: {
            opacity: 0.45,
            fontSize: 30,
            textAlign: 'center'
          }
        })
      })
      chart.render()
    }/* ,
    filterData (data) {
      if (data && data.length > 0) {
        let label = this.chartOptions.properties.label
        let value = this.chartOptions.properties.value
        data.forEach(item => {
          if (label) {
            item.label = item[label]
          }
          if (value) {
            item.value = item[value]
          }
        })
      }
      this.chartData = data || []
    } */
  }
}
</script>

<style scoped>

</style>



