<template>
  <!--<div :style="'width: 100%; height:' + height + 'px; background: white;'">
    <div v-show="chartData.length == 0" class="no-data" :style="'text-align: center;color: rgba(0, 0, 0, 0.45);height:' + height + 'px;line-height:' + height + 'px;'" v-html="chartOptions.chart.noDataText"></div>
    <div v-show="chartData.height != 0" :id="chartId"></div>
  </div>-->
  <div :id="chartId"></div>
</template>

<script>
import G2 from '@antv/g2'
import util from './utils'
/**
 * 基础 G2 Chart 封装, 用于统一 图表的 方法（类似接口）、数据格式及格式转换
 * 目的在于减少业务中图表应用的统一管理，和后续后端支持
 * https://v-charts.js.org/#/data
 * https://blog.csdn.net/weixin_42275932/article/details/90080199
 *
 * 关于Vue继承需要注意的：
 * 1、目前我没找到类似于 this.callParent() 调用父类方法方式;
 * 2、已知 template 要重写则需要全量覆盖方式
 *
 */
export default {
  name: 'BaseChart',
  props: {
    height: {
      type: Number,
      default: 400
    },
    // id: {
    //   type: String,
    //   default: function () {
    //     return util.id()
    //   }
    // },
    // 源数据
    data: {
      type: Array,
      default: function () {
        return []
      }
    },
    // 图表属性设置
    options: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  watch: {
    data (value) {
      this.filterData(value)
      this.reload()
    },
    options (value) {
      console.log('options', value)
    }
  },
  data () {
    // this.filterData([].concat(this.data)) 这里初始数据格式待商榷
    return {
      chartId: util.id(),
      chartEle: null,
      // 暂时没想好解决deep copy方式, 只做一级 copy 处理
      chartOptions: Object.assign({
        chart: {
          title: '',
          captions: '',
          padding: undefined,
          emptyDataText: '(⊙﹏⊙) 没有展示数据',
          dimension: [],
          metrics: []
        },
        value: {
          formatter: null,
          suffix: '',
          prefix: ''
        },
        properties: {
          label: null,
          value: null
        }
      }, this.options || {}),
      chartData: this.data
    }
  },
  mounted () {
  },
  methods: {
    load (params) {
      console.debug('子类自行扩展', params)
    },
    reload (params) {
      console.debug('子类自行扩展，参数初始化', params)
    },
    render () {
      if (this.chartEle != null) {
        this.chartEle.render()
      }
    },
    getChart () {
      if (this.chartEle != null) {
        this.chartEle.clear() // 目测效率更高
        return this.chartEle
        // this.chartEle.destroy()
        // this.chartEle = null
      }
      this.chartEle = new G2.Chart(this.copy({
        container: this.chartId,
        forceFit: true,
        height: this.height
      }, {
        padding: this.chartOptions.chart.padding
      }))
      return this.chartEle
    },
    // 过滤数据,做数据格式转换
    filterData (data) {
      let name = this.$options.name
      if (data && data.length > 0) {
        if (['CDonut', 'CPie', 'CBar'].indexOf(name) > -1) {
          let label = this.chartOptions.properties.label
          let value = this.chartOptions.properties.value
          data.forEach(item => {
            if (label) {
              item.label = item[label]
            }
            if (value) {
              item.value = item[value]
            }
          })
        }
      }
      return (this.chartData = data || [])
    },
    copy (target, source, excludeKeys) {
      excludeKeys = excludeKeys || []
      for (var key in (source || {})) {
        if (typeof source[key] !== 'undefined' && excludeKeys.indexOf(key) === -1) {
          target[key] = source[key]
        }
      }
      return target
    }
  }
}
</script>

<style>
  .chart{}
  .chart-message-area {
    position: absolute;top: 50%;left: 50%;width:120px;line-height:30px;margin-left:-60px;margin-top:-15px;z-index: 100;font-size: 12px;
  }
</style>



