<template>
  <div :id="chartId" style="background-color: white;text-align: center;">
    <span v-if="chartData.length == 0" class="chart-message-area" style="">{{chartOptions.chart.emptyDataText}}</span>
  </div>
</template>

<script>
import BaseChart from './BaseChart'
import DataSet from '@antv/data-set'
import util from './utils'
/**
 * 说明: 1 维统计图表， 多指标， 适用于固定指标内容 的场景（目前考虑）
 * 维度 dimension ：是数据的属性
 * 指标 metrics ：是量化衡量标准
 */
export default {
  name: 'CArea',
  extends: BaseChart,
  data () {
    return {}
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      this.load()
    },
    load () {
      this.draw()
    },
    draw () {
      if (typeof this.chartOptions.chart.padding === 'undefined') {
        this.chartOptions.chart.padding = [30, 'auto', 70, 'auto']
      }
      // 指标，维度解析
      let dimension = this.chartOptions.chart.dimension
      let metrics = this.chartOptions.chart.metrics
      let value = this.chartOptions.value
      let exValue = this.copy({}, value, ['formatter', 'prefix', 'suffix'])
      let chart = this.getChart()
      var dv = new DataSet.View().source(this.chartData)
      dv.transform({
        type: 'fold',
        fields: metrics, // fields 指标, year 维度
        key: 'type',
        value: 'value'
      })

      chart.source(dv, {
        value: this.copy({
          formatter: value.formatter || function formatter (val) {
            return (value.prefix || '') + val + (value.suffix || '')
          }
        }, exValue)
      })
      chart.tooltip({
        crosshairs: true
      })
      chart.area().position(dimension[0] + '*value').color('type', util.Color.getColors('area')).shape('smooth')
      chart.line().position(dimension[0] + '*value').color('type', util.Color.getColors('area')).size(2).shape('smooth')
      chart.render()
    }
  }
}
</script>

<style scoped>

</style>



