<template>
  <div :id="chartId" style="background-color: white;text-align: center;">
    <span v-if="chartData.length == 0" class="chart-message-area" style="">{{chartOptions.chart.emptyDataText}}</span>
  </div>
</template>
<script>
import BaseChart from './BaseChart'
// import G2 from '@antv/g2'

// 可以通过调整这个数值控制分割空白处的间距，0-1 之间的数值
/* var sliceNumber = 0.003

// 自定义 other 的图形，增加两条线
G2.Shape.registerShape('interval', 'sliceShape', {
  draw: function draw (cfg, container) {
    var points = cfg.points
    var path = []
    path.push(['M', points[0].x, points[0].y])
    path.push(['L', points[1].x, points[1].y - sliceNumber])
    path.push(['L', points[2].x, points[2].y - sliceNumber])
    path.push(['L', points[3].x, points[3].y])
    path.push('Z')
    path = this.parsePath(path)
    return container.addShape('path', {
      attrs: {
        fill: cfg.color,
        path: path
      }
    })
  }
}) */

export default {
  name: 'CDonut',
  extends: BaseChart,
  props: {
    height: {
      type: Number,
      default: 300
    }
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      this.load()
    },
    load () {
      this.draw()
    },
    draw () {
      if (typeof this.chartOptions.chart.padding === 'undefined') {
        this.chartOptions.chart.padding = [50, 0]
      }
      let total = 0
      this.chartData.forEach(item => {
        total += item.value
      })
      let radius = this.chartOptions.chart.radius
      let value = this.chartOptions.value
      let chart = this.getChart()
      chart.source(this.chartData, {
        value: {
          formatter: function formatter (val) {
            val = parseFloat((val / (total || 1)) * 100).toFixed(2) + '%'
            return val
          }
        }
      })
      chart.coord('theta', {
        radius: radius || 0.65,
        innerRadius: radius ? (radius - 0.05) : 0.6
      })
      chart.tooltip({
        showTitle: true
      })
      chart.intervalStack().position('value').color('label').label('value', {
        formatter: (value.formatter || function (val, item) {
          return item.point.label + ': ' + item.point.value + (value.suffix)
        })
      }).shape('sliceShape')

      chart.render()
    }
  }
}
</script>

<style scoped>

</style>



